table.meta-matrix {
  width: 100%;
  border-spacing: 0;
  border-collapse: separate;
}

table.meta-matrix p {
  margin: 0.3em;
}

table.meta-matrix th {
  font-size: 0.9em;
  background-color: #f5f5f5;
  border: 1px solid #ddd;
  padding: 8px;
  text-align: center;
  vertical-align: top;
}

/* make thead and th in tbody sticky */
table.meta-matrix thead th {
  position: sticky;
  top: 0;
  z-index: 2;
}

table.meta-matrix tbody th {
  position: sticky;
  left: 0;
  z-index: 1;
  background-color: #f5f5f5;
  text-align: left;
}

table.meta-matrix td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: center;
  vertical-align: center;
}

td > p.meta-winrate {
  font-size: 1.2em;
  font-weight: bold;
}
