.deck-container {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
  background-color: #e0e0e0;
  border: 1px solid #ccc;
  border-radius: 10px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.deck-stats {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 10px;
}

.deck {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  padding: 16px;
  flex-wrap: wrap;
}

.card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 4px;
}

img.card-img {
  width: 5em;
  height: 7em;
  padding: 0 1em;
}
